module.exports = "<svg touch-action=\"none\">\r\n    <g class=\"mini-edges\" ng-style=\"{transform: transform, strokeWidth:1.5/scale}\">\r\n        <path miniconnector ng-repeat=\"edge in visualData.connections\"></path>\r\n    </g>\r\n</svg>\r\n<div class=\"minimap-nodes\" ng-style=\"{transform: transform}\">\r\n    <div minicomponent ng-repeat=\"data in visualData.components track by data.id\" ng-style=\"{transform:'translate(' + data.position.x + 'px, ' + data.position.y + 'px)', width:offsetWidth, height:offsetHeight, borderWidth:1/scale}\"\r\n        ng-switch on='data.type' class=\"{{data.type}}-component mini-component\"></div>\r\n    <div class=\"viewbox\"></div>\r\n</div>";
