module.exports = "<div class=\"container-fluid\">\r\n  <div class=\"row-fluid\">\r\n    <div ng-repeat=\"record in directoryContent\" class=\"col-xs-4 col-sm-3 col-md-2\" directoryfile ng-class=\"(selectedFile == record) ? 'selected':''\"\r\n      draggable=\"true\" ng-click=\"selectFile(record)\"></div>\r\n  </div>\r\n</div>\r\n<div class=\"transfer-buttons\">\r\n  You can also download, and upload files by dragging files from, and to the desktop. &nbsp;&nbsp;&nbsp;&nbsp;\r\n  <a href=\"{{selectedFile.downloadurl}}\" download=\"{{selectedFile.filename}}\" ng-disabled=\"!selectedFile\" class=\"download btn\"> Download File </a>\r\n  <div class=\"upload btn\"> Upload File </div>\r\n</div>";
